//Sample component
nav {
  width: 100%;
  height: 100px;
  padding: 2rem;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: white;

  @include breakpoint-up("medium") {
    height: 125px;
  }

  @include breakpoint-up("large") {
    height: 180px;
    padding: 3rem 5rem;
  }

  .nav-left {
    img {
      width: 100px;
      height: auto;

      @include breakpoint-up("medium") {
        height: 125px;
      }

      @include breakpoint-up("large") {
        width: 180px;
      }
    }
  }

  .nav-right {
    .btn-navs-wrapper {
      display: flex;
      flex-direction: row;
      gap: 0.5rem;
      justify-content: center;
      align-self: center;

      img {
        @include breakpoint-up("medium") {
          width: 32px;
        }
        @include breakpoint-up("large") {
          width: 40px;
        }
      }
    }
  }
}
